import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { AlertController, ModalController } from '@ionic/angular';
import { MapasService } from 'src/app/providers/Api/Mapar.service';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { UsuariosLoginService } from '../../../providers/Api/UsuariosLogin.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { MapaeditablePage } from '../mapaeditable/mapaeditable.page';


@Component({
  selector: 'app-sedes-mapa',
  templateUrl: './sedes-mapa.page.html',
  styleUrls: ['./sedes-mapa.page.scss'],
})
export class SedesMapaPage implements OnInit {

  cargando = false;

  vista = 3;
  negocios = [];
  paises = [];
  ciudades = [];
  pais = '';
  paiss = '';
  miciudad = '';
  escogidos = [];

  ciudad = false;
  ncompleto = '';

  sedes = [];

  imagen = '';
  lat = 0;
  lng = 0;

  mensaje = '';
  draggable = true;

  editable = false;
  id = '';

  imagenLocal = '';

  hasta = '';
  desde = '';
  de = '';
  a = '';

  
  @ViewChild('mapa') mapa: ElementRef;
  constructor(
    private almacenamiento: InicioService,
    private alertCtrl: AlertController,
    private apiMapas: MapasService,
    private apiUsuario: UsuariosLoginService,
    private toastService: ToastServices,
    private geoReferencia: Geolocation,
    private modalCtrl: ModalController
  ) { }

  ngOnInit() {
  }

 ionViewWillEnter() {
  this.vista = 1;
  this.listarPaises();
  this.listarNegocios();
  this.traerSedes();
  this.llamarFoto();
 }

 

async llamarFoto() {

const info = await this.almacenamiento.obtenerToken();

if ( info ) {
  this.apiUsuario.viewImagen(info.token).subscribe((url: any) => {
    // // console.log(url);
    this.imagen = `https://motocaliapp.com/${url.paquete}`;
    this.imagenLocal = url.paquete;
    console.log(this.imagen)
  }, (error) => {
    // // console.log(error);
  })
}
    
  }

 
async traerSedes() {
  const info = await this.almacenamiento.obtenerToken();

  if ( info ) {
    this.apiMapas.traerSedes(info.token, {}).subscribe((data: any) => {
      if ( data ){
        this.sedes = data.response;
      } else {
        this.toastService.toastError('No se pudo traer las sedes, inténtelo más tarde');
      }
    }, (err) => {
      this.toastService.toastError('No se pudo traer las sedes, inténtelo más tarde');
    })
  }
}

 
 listarNegocios() {
  this.apiUsuario.negocios().subscribe((data: any) => {
    if (data.exe) {
      data.response.forEach((item) => {
        item.checked = false;
      })
      this.negocios = data.response;
    } else {
      this.negocios.push('Taller Mécanico');
    }
  }, (err) => {
    this.negocios.push('Taller Mécanico');
  })
}


listarPaises() {
  this.apiUsuario.listarPaises().subscribe((data: any) => {
    if (data.exe) {
      this.paises = data.response;
    } else {
      this.paises.push('Colombia');
    }
  }, (err) => {
    this.paises.push('Colombia');
  })
}


listarCiudades( event, opt? ) {
  console.log(event.detail)
  this.pais = event.detail.value.nombre;
  this.miciudad = '';
  this.cargando = true;
  this.apiUsuario.listarCiudades(event.detail.value._id).subscribe((data: any) => {
   if (data.exe) {
     this.ciudades = data.response;
     this.ciudad = true;
     if (opt) {
       
 
     }
   } else {
     this.toastService.toastError('No se pudo traer las ciudades, intente registrarse más tarde');
   }
   this.cargando = false;
 }, (err) => {
   this.toastService.toastError('No se pudo traer las ciudades, intente registrarse más tarde');
   this.cargando = false;
 })
}

seleccionarCiudad( event ) {
 this.miciudad = event.detail.value;
}


agregarNegocios( event ) {
  if (event.detail.checked) {
   if (!this.escogidos.find((item) => item === event.detail.value)) {
     this.escogidos.push(event.detail.value);
   }
  } else {
    this.escogidos = this.escogidos.filter((item) => item !== event.detail.value);
  }

  console.log(this.escogidos);
   
}

configurar() {
  // tslint:disable-next-line:max-line-length
  if (this.ncompleto !== '' && this.pais !== '' && this.miciudad !== '' && this.escogidos.length > 0 && this.de !== '' && this.a !== '' && this.desde !== '' && this.hasta !== '') {
    this.vista = 3;
    this.abrirModal();
  } else {
    this.toastService.toastError('Hay datos que no se han diligenciado correctamente');
  }
 
}

async abrirModal() {
  const modal = await this.modalCtrl.create({
    component: MapaeditablePage,
    componentProps: {
      lng: this.lng,
      lat: this.lat,
      ncompleto: this.ncompleto,
      pais: this.pais,
      miciudad: this.miciudad,
      escogidos: this.escogidos,
      de: this.de,
      a: this.a,
      desde: this.desde,
      hasta: this.hasta,
      editable: this.editable,
      id: this.id,
      imagenLocal: this.imagenLocal,
      imagen: this.imagen
    }
  })

  await modal.present();

  const info = await modal.onWillDismiss();

  if (info) {
    this.vista = 1;
  }
}


crear(){
  this.draggable = true;
  this.editable = false;
  this.escogidos = [];
  this.pais = '';
  this.miciudad = '';
  this.de = '';
  this.desde = '';
  this.a = '';
  this.hasta = '';
  this.listarNegocios()
  this.ncompleto = 'Escriba el nombre';
  this.vista = 2;
}

retroceder(){
  this.vista = 1;
}

ver( sede ) {
  this.lat = sede.ubicacion.lat;
  this.lng = sede.ubicacion.lng;

  this.abrirModal();
  }


async eliminar( sede, i ) {
    const info  = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.apiMapas.eliminarSede(info.token, {id: sede._id}).subscribe((data: any) => {
        if (data.exe) {
          this.toastService.toastExitoso('Sede eliminada');
          this.sedes.splice(i, 1);
        } else {
          this.toastService.toastError('No se pudo eliminar la sede, inténtelo más tarde');
        }
      }, (err) => {
        this.toastService.toastError('No se pudo eliminar la sede, inténtelo más tarde');
      })
    }
}

editar(sede, i) {
  const pais = this.paises.filter((item) => item.nombre === sede.pais)[0];
  console.log('PAIS', pais)
  this.paiss = pais;
  this.pais = sede.pais;
  this.editable = true;
  this.listarCiudades({detail: {value: pais}}, true);
  this.miciudad = sede.ciudad;
  this.ncompleto = sede.nombre;
  this.desde = sede.desde;
  this.hasta = sede.hasta;
  this.de = sede.de;
  this.a = sede.a;
  this.lat = sede.ubicacion.lat;
  this.lng = sede.ubicacion.lng;
  this.negocios.forEach((item) => {
    sede.tipoNegocio.forEach((it) => {
      if (item.nombre === it) {
        item.checked = true;
      }
    })
  })
  this.draggable = true;
  this.escogidos = sede.tipoNegocio;
  this.vista = 2;
  this.id = sede._id;
}




horario(event , tipo) {
  if (tipo === 'desde') {
    this.desde = event.detail.value;
  } else {
    this.hasta = event.detail.value;
  }

}

dias( event, tipo) {
  if (tipo === 'de') {
    this.de = event.detail.value;
  } else {
    this.a = event.detail.value;
  }
}



}
