import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { AlertController, ModalController } from '@ionic/angular';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';
import { MapasService } from 'src/app/providers/Api/Mapar.service';
import { UsuariosLoginService } from 'src/app/providers/Api/UsuariosLogin.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';

declare var mapboxgl;

@Component({
  selector: 'app-mapaeditable',
  templateUrl: './mapaeditable.page.html',
  styleUrls: ['./mapaeditable.page.scss'],
})
export class MapaeditablePage implements OnInit {


      @Input() lng;
      @Input() lat;
      @Input() ncompleto; 
      @Input() pais; 
      @Input() miciudad;
      @Input() escogidos; 
      @Input() de; 
      @Input() a; 
      @Input() desde;
      @Input() hasta;
      @Input() editable; 
      @Input() id; 
      @Input() imagen; 
      @Input() imagenLocal; 

  mensaje = '';
  draggable= true;
  
  @ViewChild('mapa') mapa: ElementRef;

  constructor(
    private almacenamiento: InicioService,
    private alertCtrl: AlertController,
    private apiMapas: MapasService,
    private apiUsuario: UsuariosLoginService,
    private toastService: ToastServices,
    private geoReferencia: Geolocation,
    private modalCtrl: ModalController
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.configurar();
    console.log(this.lat, this.lng, 'data', this.editable)
  }

  cerrar() {
    this.modalCtrl.dismiss()
  }
  
 async calcular() {
  try {
    console.log('entramos al click')
    const data = await this.geoReferencia.getCurrentPosition();

    if ( data ) {
    console.log(data);
    this.lat = data.coords.latitude;
    this.lng = data.coords.longitude;
    this.graficar();
  }
  } catch (error) {
    
    this.mensaje = error;
  }
}




  configurar() {
    // tslint:disable-next-line:max-line-length
    if (this.ncompleto !== '' && this.pais !== '' && this.miciudad !== '' && this.escogidos.length > 0 && this.de !== '' && this.a !== '' && this.desde !== '' && this.hasta !== '') {
      if (this.editable) {
        setTimeout(() => {
          this.graficar();
        }, 1000 )
      } else {
        this.calcular()
      }
    } else {
      this.toastService.toastError('Hay datos que no se han diligenciado correctamente');
    }
   
  }

graficar() {
  console.log('Bien');
  mapboxgl.accessToken = 'pk.eyJ1Ijoic2FudGlhZ29mZXJuYW5kZXpkZXYiLCJhIjoiY2tmYmh6cXdzMDM0bDJ2cHRucTI4cHpkaSJ9.-01EYvsLdJOU1Oy4BtwBwA';
  const mapa = new mapboxgl.Map({
  container: this.mapa.nativeElement,
  style: 'mapbox://styles/mapbox/streets-v11',
  center: [this.lng, this.lat],
  zoom: 15
  });

  mapa.addControl(new mapboxgl.NavigationControl());

  const el = document.createElement('div'); 
  el.className = 'marker'; 
  el.style.backgroundImage = 'url(' + this.imagen + ')';
  el.style.backgroundSize = '100%';
  el.style.width = 50 + 'px';
  el.style.height = 50   + 'px';

  const marker = new mapboxgl.Marker({
    element: el,
    draggable: this.draggable
  })
    .setLngLat([this.lng, this.lat])
    .addTo(mapa)

  marker.on('dragend', () => {
        const lngLat = marker.getLngLat();
        this.lat = lngLat.lat;
        this.lng = lngLat.lng;
        console.log('Longitude: ' + lngLat.lng + '<br />Latitude: ' + lngLat.lat)
      });


  
}

async editarGuardar() {
  const info  = await this.almacenamiento.obtenerToken();

  if ( info ) {
    // tslint:disable-next-line:max-line-length
    this.apiMapas.editarSede(info.token, {ubicacion: {lat: this.lat, lng: this.lng}, nombre: this.ncompleto, tipo: this.escogidos, pais: this.pais, ciudad: this.miciudad, _id: this.id, desde: this.desde, hasta: this.hasta, de: this.de, a: this.a}).subscribe((data: any) => {
      if (data.exe) {
        this.toastService.toastExitoso('Sede editada');
       
      } else {
        this.toastService.toastError('No se pudo editar la sede, inténtelo más tarde');
      }
      this.modalCtrl.dismiss();
    }, (err) => {
      this.modalCtrl.dismiss();
      this.toastService.toastError('No se pudo editar la sede, inténtelo más tarde');
    })
  }
}


async guardar() {
  const info = await this.almacenamiento.obtenerToken();

  if ( info ) {
    // tslint:disable-next-line:max-line-length
    this.apiMapas.crearSede(info.token, {ubicacion: {lat: this.lat, lng: this.lng}, nombre: this.ncompleto, tipo: this.escogidos, pais: this.pais, ciudad: this.miciudad, desde: this.desde, hasta: this.hasta, de: this.de, a: this.a}).subscribe((data: any) => {
      if ( data ){
        this.toastService.toastExitoso('Sede creada');
      } else {
        this.toastService.toastError('No se pudo crear la sede, inténtelo más tarde');
      }
      this.modalCtrl.dismiss();
    }, (err) => {
      this.modalCtrl.dismiss();
      this.toastService.toastError('No se pudo crear la sede, inténtelo más tarde');
    })
  }
}




}
